<template>
  <div class="wrap">
    <input v-model="message" placeholder="what movies to fill the void in your life?" type='text' class='searchInput' v-on:change='submit'>
    <Results :searchKey='message' :sendAnotherRequest = 'sendAnotherRequest'></Results>
  </div>
</template>


<script>
  import Results from './Results'
  export default {
    components: {
      Results
    },
    data () {
      return {
        message: '',
        sendAnotherRequest: 1
      }
    },
    methods: {
      submit: function () {
        this.sendAnotherRequest ++
      }
    }
  }
</script>

<style scoped>
  .wrap {
    width:100%;
    display:block;
  }
  .searchInput {
    width: 96%;
    height: 36px;
    border-radius: 4px;
    border-color: #00adff;
    border-width: 2px;
    border-style: solid;
    text-align:center;
    display:flex;
    justify-content: center;
    align-items: center;
    font-size:20px;
    margin-left:auto;
    margin-right: auto;
  }
  .searchInput::-webkit-input-placeholder { /* Chrome/Opera/Safari */
    font-size:20px;
  }
  .searchInput::-moz-placeholder { /* Firefox 19+ */
    font-size:20px;
  }
  .searchInput:-ms-input-placeholder { /* IE 10+ */
    font-size:20px;
  }
  .searchInput:-moz-placeholder { /* Firefox 18- */
    font-size:20px;
  }
</style>


