<template>
  <div class='wrap'>
    <transition name='fade' mode='out-in'>
      <div v-if='dataReady' class='movieList' key='content'>
        <div v-for="movie in moviesData" class='movieItem'>
          <h2>{{movie.title}}</h2>
          <img :src='movie.poster_path' alt="posters">
          <p>{{movie.overview}}</p>
        </div>
      </div>
      
      <div v-else key='loading'>
        Loading....
      </div>
    </transition>
  </div>
</template>


<script>
  import Vue from 'vue'

  export default {
    props: ['searchKey', 'sendAnotherRequest'],
    data () {
      return {
        apiKey: 'a14bab03fc759bc3fa3abcfa51cc97f8',
        dataReady: false,
        moviesData: [],
        imagePath: 'https://image.tmdb.org/t/p/w185_and_h278_bestv2'
      }
    },
    methods: {
      modifyImgPath (arr) {
        return arr.map((element) => {
          element.poster_path = this.imagePath + element.poster_path
          return element
        })
      },
      defaultRequest () {
        const params = [
          `api_key=${this.apiKey}`,
          'sort_by=vote_average.desc',
          'page=1',
          'vote_count.gte=1000'
        ]
        Vue.axios.get(`https://api.themoviedb.org/3/discover/movie?${params.join('&')}`)
          .then((response) => {
            this.moviesData = this.modifyImgPath(response.data.results)
            this.dataReady = true
            console.log(response.data.results)
          })
          .catch((error) => {
            console.log(error)
          })
      },
      searchSpeific () {
        this.dataReady = false
        const params = [
          `api_key=${this.apiKey}`,
          `query=${this.searchKey}`,
          'page=1'
        ]
        Vue.axios.get(`https://api.themoviedb.org/3/search/movie?${params.join('&')}`)
          .then((response) => {
            this.moviesData = this.modifyImgPath(response.data.results)
            this.dataReady = true
            console.log(response.data.results)
          })
          .catch((error) => {
            console.log(error)
          })
      }
    },
    watch: {
      sendAnotherRequest: function (newVal, oldVal) {
        if (this.searchKey === '') {
          this.defaultRequest()
        } else {
          this.searchSpeific()
        }
      }
    },
    mounted: function () {
      this.defaultRequest()
    }
  }
</script>

<style scoped>
  .fade-enter-active, .fade-leave-active {
    transition: opacity 0.3s
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0
  }
  .wrap {
    width:98%;
    margin-left:auto;
    margin-right:auto;
    height:auto;
  }
  .movieList {
    margin-top:20px;
    padding:20;
    display:flex;
    flex-direction:row;
    justify-content: center;
    align-items: flex-start;
    width:100%;
    flex-wrap:wrap;
    background-color:rgba(0, 161, 255, 0.08);
    border-radius:4px;
    padding:20px;
    background-color: #2c3e50;
    color: white;
  }
  .movieItem {
    width:46%;
  }
</style>


